onBeforeMount(async () => {  
  const resp = await axios.get(`${config.appUrl}/region/list`);
  const locations = resp.data;
  data.value.regions = locations.reduce(
    (acc: {[key: number]: string}, obj: {id: number, region: string}) => {
    acc[obj.id] = obj.region;
